.container {
  padding: 0 2.5rem;
  max-width: 81.25rem;
  margin: 0 auto;
}
.container-md {
  max-width: 62.5rem;
  margin: 0 auto;
}

.flex {
  display: flex;
}
.flex-row {
  flex-direction: row;
}
.flex-col {
  flex-direction: column;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-c {
  justify-content: center;
}
.flex-ai-c {
  align-items: center;
}
.flex-ai-s {
  align-items: flex-start;
}